.root {
   :global {
      li {
         display: inline-flex;
         justify-content: center;
         align-items: center;

         &.selected {
            a {
               background-color: #fe5f1e;
               color: #fff;
            }
         }
      }

      a {
         display: inline-block;
         text-align: center;
         color: #fe5f1e;
         width: 45px;
         height: 45px;
         padding: 10px;
         border: 1px solid #fe5f1e;
         border-radius: 30px;
         margin-right: 10px;
         cursor: pointer;

         &:hover {
            background-color: #fe5f1e;
            color: #fff;
         }
      }
   }
}
